<template>
<modal v-model:visible="modalConfirmationVisible" modalWidth="22em">
  <template #content>
    <div class="modal-text">Ви точно хочете видалити <b>{{ prefabName }}</b>?</div>
  </template>
  <template #buttons>
    <button class="button-small" :style="{ background: colorTheme }" @click="deletePrefab(prefabName)">Так</button>
    <button class="button-small" :style="{ background: colorTheme }" @click="modalConfirmationVisible = false">Ні</button>
  </template>
</modal>

<main-layout :backgroundImage="backgroundURL" backgroundOffset="15">

  <template #header>
    <div class="module-section">
      <div class="module-navbar">
        <router-link to="modules" class="link">
          <span class="material-icons">arrow_back</span>&nbsp;
          Назад до списку курсів
        </router-link>
        <span></span>
      </div>
      <div class="module-course line-r">НМТ-2022</div>
      <div class="module-name">{{ moduleName }}</div>
    </div>
  </template>

  <template #body>
    <div class="lessons-section">
      <div class="relative" v-for='(lesson, j) in lessons' :key='lesson.name'>
        <lesson-card :lesson='{ ...lesson, index: j+1 }' @click='launchPrefab(lesson.name)'/>
        <i class="material-icons-outlined centered icon-delete" @click='showConfirmationModal(lesson.name)'>delete_forever</i>
      </div>
    </div>
    <div class="create-section">
      <div v-if='lessons.length < 5'>
        <ul>
          <li>Один тест містить 20 завдань на різні теми</li>
          <li>Ви матимете по одній спробі на завдання</li>
          <li>Рекомендований час винонання &ndash; 40 хвилин</li>
        </ul><br>
        <button class="button-small"
         :style='{ background: colorTheme }' 
         @click='createPrefab(`Підсумковий тест ${lessons.length + 1}`)'>
          Створити тест
        </button>
      </div>
      <div v-else>Щоб створити новий тест, видаліть один з існуючих</div>
    </div>
  </template>

</main-layout>
</template>

<script>
import modal from '../components/modal-basic.vue'
import controller from '../api/controller.js'
import mainLayout from '@/layouts/main-layout.vue'
import lessonCard from '@/components/lesson-card.vue'
export default {
  components: { mainLayout, lessonCard, modal },
  data() { return {
    prefabName: undefined,
    modalConfirmationVisible: false,
    moduleName: localStorage.getItem('moduleName'),
    moduleId: localStorage.getItem('moduleId'),
    colorTheme: localStorage.getItem('colorTheme'),
  }},
  computed: {
    lessons() { return this.$store.state.lessons },
    backgroundURL() { return this.$cloudinary + this.moduleId + '.jpg' },
  },
  methods: {
    launchPrefab(prefabName, lessonId) {
      localStorage.setItem('lessonName', prefabName)
      this.$router.push("prefab")
    },
    async createPrefab(prefabName) {
      await controller.createPrefab(prefabName)
      this.$store.dispatch('getPrefabs')
    },
    async deletePrefab(prefabName) {
      await controller.deletePrefab(prefabName)
      this.$store.dispatch('getPrefabs')
      this.modalConfirmationVisible = false
    },
    showConfirmationModal(prefabName) {
      this.modalConfirmationVisible = true
      this.prefabName = prefabName
    }
  },
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
ul {
  margin: 0;
  padding: 0;
  list-style-position: inside;
}

.module-section {
  width: min(70em, 100%);
  margin: auto;
  .module-navbar {
    display: flex;
    align-items: center;
    column-gap: .5em;
    margin-bottom: max(7em, 15vmin);
    font-size: .9em;
    color: #fff;
    *:last-child { margin-left: auto }
  }
  .module-course {
    margin-bottom: 1em;
    font-weight: bold;
    color: #fffa;
  }
  .module-name {
    font-family: 'Oswald';
    font-size: clamp(1.7em, 5.25vmin, 2.5em);
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
  }
  .bar-wrapper {
    height: .3em;
  }
}

.lessons-section {
  width: min(70em, 100%);
  margin: auto;
  margin-bottom: 3vmin;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0,0,0,.12);
}

.create-section {
  width: min(70em, 100%);
  margin: auto;
  padding: 5px;
  box-sizing: border-box;
  background: #fff;
  text-align: center;
  & > *:first-child {
    width: 100%;
    height: 100%;
    padding: 1.5em 0;
    box-sizing: border-box;
    border: 2px dashed #0002;
    color: gray;
  }
}

.icon-delete {
  left: calc(100% - 32px);
  padding: 8px;
  opacity: .12;
  &:hover {
    color: red;
    opacity: 1;
  }
}

</style>